body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.pizza-info {
  max-width: 540px;
  margin-left: auto;
  margin-right: auto;
}
.pizza-rate {
  text-align: center;
}
.col-2 {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  max-width: 540px;
  margin-left: auto;
  margin-right: auto;
  color: red;
}

.extra-items {
  display: flex;
  flex-direction: column;
  max-width: 720px;
  margin-left: auto;
  margin-right: auto;
}
.order-note {
  max-width: 540px;
  display: flex;
  justify-content: left;
  margin-left: auto;
  margin-right: auto;
  .ty-logo {
    background-color: #ce2829;
    display: flex;
    justify-content: center;
  }
  .ty-logo img {
    width: 30vh;
    padding-top: 2rem;
  }
}

.body {
  background-image: url(/static/media/mvp-banner.e1143cc0.png);
  height: 100vh;
  width: 100%;
  background-position: center center;
  background-size: cover;
  display: flex;
  justify-content: center;

  text-align: center;
  color: white;
  font-family: "Barlow", sans-serif;
}
.body p {
  font-size: 3rem;
}
.body img {
  width: 270px;
  margin-bottom: 10%;
}
.content h3 {
  font-family: "Satisfy", sans-serif;
  color: #fdc913;
}
.orderPizza {
  background-color: #fdc913;
  color: black;
  padding: 10px 30px;
  text-decoration: none;
  font-weight: 600;
  font-size: 0.8rem;
  border-radius: 20px;
}
.orderPizza:hover {
  color: whitesmoke;
  background-color: red;
  color: #faf7f2;
}

.success {
  background-color: #ce2829;
  height: 100vh;
  text-align: center;
}
.success p {
  color: #faf7f2;
  font-size: 4rem;
  font-family: "Barlow Condensed", sans-serif;
  text-decoration: none;
}

.pizza-form {
  background-color: white;
  justify-content: center;
  text-align: center;
  align-items: center;
  width: 100%;
  display: flex;
  flex-direction: column;
}
.pizza-order nav {
  margin-bottom: 20px;
  display: flex;
  justify-content: center;
}
.pizza-order nav a {
  text-decoration: none;
  color: #333;
  margin-right: 10px;
}
.itemList {
  color: black;
  display: flex;
  margin-left: auto;
  margin-right: auto;
  justify-content: center;
  width: 33%;
  flex-wrap: wrap;
}
.amountAndCost {
  display: flex;
  flex-direction: row;
  width: 720px;
}
.checkboxes {
  display: flex;
}
.col-2 {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.radio {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.checkboxALL {
  display: flex;
  justify-content: center;
  flex-flow: column wrap;
}
.radio-select {
  display: flex;
  flex-direction: row;
}
.pizzaBody {
  width: 720px;
  margin-left: auto;
  margin-right: auto;
  justify-content: center;
  display: flex;
  background-color: yellow;
}
.header nav {
  background-color: #ce2829;
  padding-top: 1rem;
  justify-content: center;
  align-items: center;
  display: flex;
  font-size: 0.5rem;
}

.pizza-name {
  justify-content: center;
  display: flex;
  font-size: 0.5rem;
}

.ty-logo {
  background-color: #ce2829;
  text-align: center;
  padding-top: 5rem;
}

.footer-row {
  border: 2px, solid black;
  border-style: solid;
  border-width: 1px 1px;
}
.footer-columns {
  border: 2px, solid black;
  border-style: solid;
  border-width: 1px 1px;
}
.footer-columns img {
  max-width: 200px;
  background-color: black;
}

